/* ---------------
   # Overlay Menu
--------------- */

.hamburger {
	z-index: 1; 
	right: 55px; 
	top: 52px; 
	position: absolute; 
	color: white; 
	cursor:pointer;
  }

.overlay {
  height: 0%;
  width: 100%;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  background-color: rgb(255,255,255);
  background-color: rgba(255,255,255, 0.9);
  overflow-y: hidden;
  transition: 0.5s;
  }

.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
  }

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 28px;
  color: #818181;
  display: block;
  transition: 0.3s;
	text-align: center;
	padding-left:60px;
  }

.overlay a:hover, .overlay a:focus {
  color: #000;
  }

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 70px;
  }

@media screen and (max-height: 450px) {
  .overlay {overflow-y: auto;}
  .overlay a {font-size: 20px}
  .overlay .closebtn {
      font-size: 40px;
      top: 0px;
      right: 30px;
      }
  }

